body {
  margin: 0;
  font-family: Arial, sans-serif;
  text-align: center;
}

.navbar {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  display: flex;
  justify-content: center;
  background: #333;
  padding: 10px;
  z-index: 1000;
}

.navbar a {
  color: white;
  background: none;
  border: none;
  padding: 10px 20px; /* Add padding to the buttons */
  cursor: pointer;
}

.navbar a:hover {
  background: #555;
}

.background-video {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
  z-index: -1;
}

.animated-background {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(45deg, #ff9a9e, #fad0c4);
  animation: gradientAnimation 10s infinite alternate;
  z-index: -1;
}

@keyframes gradientAnimation {
  0% {
    background-position: left;
  }
  100% {
    background-position: right;
  }
}

.splash {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 100vh;
  padding-top: 60px;
}

.splash button {
  padding: 10px 20px;
  font-size: 16px;
  background: #007bff;
  color: white;
  border: none;
  cursor: pointer;
  margin-top: 20px;
}

.splash button:hover {
  background: #0056b3;
}

.header-wrapper {
  position: relative;
  display: flex;
  height: 100vh;
  width: 100%;
  justify-content: center; /* Center horizontally */
  align-items: center; /* Center vertically */
}

.main-info {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  width: 100%;
  padding: 0 20px; /* Add some padding for smaller screens */
}

/* Media query for smaller screens */
@media (max-width: 768px) {
  .header-wrapper {
    height: 100vh; /* Ensure the header takes up the full viewport height */
    padding: 20px 0; /* Add padding for better spacing on smaller screens */
  }

  .main-info {
    font-size: 14px; /* Adjust font size */
    padding: 0 15px; /* Reduce padding */
  }
}

/* Additional adjustments for very small screens */
@media (max-width: 480px) {
  .main-info {
    font-size: 12px; /* Further reduce font size */
    padding: 0 10px; /* Further reduce padding */
  }
}

.enter-button {
  background-color: #007bff;
  color: white;
  border: none;
  padding: 15px 25px;
  font-size: 18px;
  font-weight: bold;
  cursor: pointer;
  border-radius: 8px;
  transition: background-color 0.3s;
  margin-bottom: 20px;
}

.enter-button:hover {
  background-color: #0056b3;
}

.enter-button h1 {
  margin: 0;
}

/* Styling for the form and inputs */
.password-form {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.password-input {
  padding: 12px 20px;
  font-size: 16px;
  border: 2px solid #ccc;
  border-radius: 8px;
  margin-bottom: 15px;
  width: 100%;
  max-width: 300px;
  box-sizing: border-box;
  transition: border-color 0.3s;
}

.password-input:focus {
  outline: none;
  border-color: #007bff;
}

/* Styling for the "Enter with a password" button */
.password-button {
  background-color: #28a745;
  color: white;
  border: none;
  padding: 12px 25px;
  font-size: 16px;
  font-weight: bold;
  cursor: pointer;
  border-radius: 8px;
  transition: background-color 0.3s;
}

.password-button:hover {
  background-color: #218838;
}
.gradient-background {
  height: 100vh;
  width: 100%;
  background: linear-gradient(
    to right,
    #6a11cb,
    #2575fc
  ); /* Example gradient */
  display: flex;
  justify-content: center;
  align-items: center;
  color: white;
  font-family: Arial, sans-serif;
  overflow: hidden;
}

/* Optional: Add some basic style to inner content */
.gradient-background h1 {
  font-size: 2rem;
  text-shadow: 2px 2px 5px rgba(0, 0, 0, 0.5);
}

.section {
  width: 100%;
  min-height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 20px;
  box-sizing: border-box;
  scroll-margin-top: 60px; /* Offset for sticky navbar */
  background-color: rgba(0, 0, 0, 0.542); /* Slightly opaque background */
  border-bottom: 1px solid rgba(0, 0, 0, 0.1); /* Optional subtle divider */
  color: white;
}

html {
  scroll-behavior: smooth;
}

.social-icons {
  display: flex;
  gap: 20px;
  margin-top: 20px;
}

.social-icons a {
  color: white;
  font-size: 24px;
  transition: color 0.3s ease;
}

.social-icons a:hover {
  color: #1da1f2; /* Example hover color */
}
